@use 'variables' as *;

.banner {
  height: 68vh;
  min-height: 537px;
  background: url(/public/images/mobile/image-header.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: $white;
  display: flex;
  flex-direction: column;
  width: 100%;

  @media (min-width: 768px) {
    background: url(/public/images/desktop/image-header.jpg);
    height: 60vh;
    min-height: 785px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.header {
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 24px;

  &__logo {
    color: $white;
  }
}

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;

  &__title {
    margin-top: -3rem;
    font-size: 2.5rem;
    text-align: center;
  }

  &__arrow_down {
    margin-top: 3rem;
  }

  .features {
    display: flex;
    flex-direction: column;
  }
}

.transform {
  text-align: center;
  display: flex;
  flex-direction: column;

  .container {
    min-height: 450px;
  }

  &__text {
    padding: 36px 0;
  }

  &__h1 {
    font-size: 2rem;
    color: $desat-blue;
  }

  &__info {
    margin: 24px 0;
    color: $dark-grayish-blue;
  }

  &__link {
    font-family: 'Fraunces', serif;
    font-weight: 700;
  }
}

.stand_out {
  text-align: center;
  display: flex;
  flex-direction: column;

  .container {
    min-height: 450px;
  }

  &__text {
    padding: 36px 0;
  }

  h2 {
    font-size: 2rem;
    color: $desat-blue;
  }

  &__info {
    margin: 24px auto;
    color: $dark-grayish-blue;
    max-width: 400px;
  }

  &__link {
    font-family: 'Fraunces', serif;
    font-weight: 700;
  }

  .link::before {
    background-color: $soft-red;
  }
}

.service {
  min-height: 600px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
  padding: 0 24px;

  &__text {
    margin-bottom: 3.75rem;
  }

  h2 {
    font-size: 1.75rem;
  }
}

.graphic-design {
  background-image: url(/public/images/mobile/image-graphic-design.jpg);
  background-position: center;
  background-size: cover;
  color: $dark-cyan;
}

.photography {
  background-image: url(/public/images/mobile/image-photography.jpg);
  background-position: center;
  background-size: cover;
  color: $dark-blue;
}

.testimonials {
  text-align: center;
  padding: 68px 0;

  &__content {
    display: flex;
    flex-direction: column;
    gap: 64px;
  }

  h2 {
    color: $grayish-blue;
    text-transform: uppercase;
    letter-spacing: 0.15rem;
    margin-bottom: 62px;
  }
  img {
    border-radius: 99999px;
    margin: 0 auto 38px auto;
    width: 72px;
  }
  p {
    color: $dark-grayish-blue;
    margin-bottom: 40px;
  }
  h3 {
    color: $desat-blue;
    margin-bottom: 14px;
  }
  span {
    color: $blue;
  }
}

.image-gallery {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;

  img {
    margin: 0 auto;
  }
}

.footer {
  color: $dark-cyan;
  text-align: center;
  padding: 68px 0;
  background-color: $medium-dark-cyan;
  a {
    color: $dark-cyan;
  }
  svg {
    fill: $dark-cyan;
    margin: 0 auto 44px auto;
  }
  &__links {
    margin-bottom: 90px;
    display: flex;
    justify-content: center;
    gap: 56px;
  }
  &__socials {
    display: flex;
    justify-content: center;
    gap: 28px;
  }
}
