*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  box-sizing: border-box;
}

body {
  line-height: 1.5;
  height: 100%;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

a {
  text-decoration: none;
}

h1 {
  font-family: "Fraunces", serif;
  font-weight: 900;
}

h2,
h3,
h4 {
  font-family: "Fraunces", serif;
  font-weight: 700;
}

.link {
  position: relative;
  color: #23303e;
}

.link::before {
  content: "";
  position: absolute;
  left: -7px;
  right: -7px;
  bottom: -2px;
  height: 10px;
  transition: opacity 0.6s cubic-bezier(0.53, 0.21, 0, 1);
  background-color: #fad400;
  opacity: 0.3;
  border-radius: 99px;
  z-index: -10;
}

.link:hover::before {
  opacity: 0.8;
}

body {
  font-family: "Barlow", sans-serif;
  font-weight: 600;
  font-size: 18px;
}

.container {
  width: 100%;
  max-width: 1440px;
  padding: 0 24px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.flex {
  display: flex;
}

.items-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}

.justify-between {
  justify-content: space-between;
}

.flex-col {
  flex-direction: column;
}

.flex-row {
  flex-direction: row;
}

.banner {
  height: 68vh;
  min-height: 537px;
  background: url(/public/images/mobile/image-header.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  display: flex;
  flex-direction: column;
  width: 100%;
}
@media (min-width: 768px) {
  .banner {
    background: url(/public/images/desktop/image-header.jpg);
    height: 60vh;
    min-height: 785px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.header {
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 24px;
}
.header__logo {
  color: white;
}

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}
.hero__title {
  margin-top: -3rem;
  font-size: 2.5rem;
  text-align: center;
}
.hero__arrow_down {
  margin-top: 3rem;
}
.hero .features {
  display: flex;
  flex-direction: column;
}

.transform {
  text-align: center;
  display: flex;
  flex-direction: column;
}
.transform .container {
  min-height: 450px;
}
.transform__text {
  padding: 36px 0;
}
.transform__h1 {
  font-size: 2rem;
  color: #23303e;
}
.transform__info {
  margin: 24px 0;
  color: #5a636c;
}
.transform__link {
  font-family: "Fraunces", serif;
  font-weight: 700;
}

.stand_out {
  text-align: center;
  display: flex;
  flex-direction: column;
}
.stand_out .container {
  min-height: 450px;
}
.stand_out__text {
  padding: 36px 0;
}
.stand_out h2 {
  font-size: 2rem;
  color: #23303e;
}
.stand_out__info {
  margin: 24px auto;
  color: #5a636c;
  max-width: 400px;
}
.stand_out__link {
  font-family: "Fraunces", serif;
  font-weight: 700;
}
.stand_out .link::before {
  background-color: #fe7867;
}

.service {
  min-height: 600px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
  padding: 0 24px;
}
.service__text {
  margin-bottom: 3.75rem;
}
.service h2 {
  font-size: 1.75rem;
}

.graphic-design {
  background-image: url(/public/images/mobile/image-graphic-design.jpg);
  background-position: center;
  background-size: cover;
  color: #25564b;
}

.photography {
  background-image: url(/public/images/mobile/image-photography.jpg);
  background-position: center;
  background-size: cover;
  color: #19536b;
}

.testimonials {
  text-align: center;
  padding: 68px 0;
}
.testimonials__content {
  display: flex;
  flex-direction: column;
  gap: 64px;
}
.testimonials h2 {
  color: #818498;
  text-transform: uppercase;
  letter-spacing: 0.15rem;
  margin-bottom: 62px;
}
.testimonials img {
  border-radius: 99999px;
  margin: 0 auto 38px auto;
  width: 72px;
}
.testimonials p {
  color: #5a636c;
  margin-bottom: 40px;
}
.testimonials h3 {
  color: #23303e;
  margin-bottom: 14px;
}
.testimonials span {
  color: #a7abae;
}

.image-gallery {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}
.image-gallery img {
  margin: 0 auto;
}

.footer {
  color: #25564b;
  text-align: center;
  padding: 68px 0;
  background-color: #458c7e;
}
.footer a {
  color: #25564b;
}
.footer svg {
  fill: #25564b;
  margin: 0 auto 44px auto;
}
.footer__links {
  margin-bottom: 90px;
  display: flex;
  justify-content: center;
  gap: 56px;
}
.footer__socials {
  display: flex;
  justify-content: center;
  gap: 28px;
}